var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{attrs:{"id":"about"}},[_c('div',{attrs:{"id":"content"}},[_c('v-app',[_c('vue-aos',{attrs:{"animation-class":"fadeIn animated"}},[_c('v-card',{staticClass:"about"},[_c('kinesis-container',[_c('kinesis-element',{attrs:{"strength":10}},[_c('v-img',{attrs:{"src":require("../assets/126326514_3530834480343077_5722785913041014548_o.jpg"),"width":"500"}})],1)],1),_c('pre',[_c('h1',[_vm._v("品 牌 故 事")]),_c('span',[_vm._v("story")]),_c('br'),_c('br'),_c('br'),_c('p',[_vm._v("因為愛上攝影而踏上旅程的小雞，觀景窗帶領著她探索這個世界，這一路上她遇到各式各樣的人，也參與了許多故事。\n攝影陪著小雞一路成長，在每個故事的尾聲她總是收到人們贈與的信物，她小心翼翼地收藏著，她說那些都是情感所幻化 ...\n▍ 來自森林深處的松針\n▍ 爺爺口袋裡的松果\n▍ 北方國度的婚禮捧花\n▍ 詩唱班童帽上的羽毛\n▍ 哼著歌的男孩替女孩繫上的胸花\n▍ 偎在壁爐旁的奶奶手捻著麻繩\n▍ 南國妹妹摘的野生雛菊\n▍ 農場婦人收集穀物\n▍ 豐收季節的麥穗\n▍ 鑲滿累累果實的樹枝\n8年的旅程繞了一圈回到家鄉，收藏的信物也繞成了一個圓滿的花環，掛在牆上滿載著情感.....")])])],1)],1),_c('v-divider'),_c('vue-aos',{attrs:{"animation-class":"fadeIn animated"}},[_c('v-card',{staticClass:"about concept mb-10"},[_c('pre',[_c('h1',{staticClass:"concept"},[_vm._v("品 牌 理 念")]),_c('span',{staticClass:"concept_span"},[_vm._v("concept")]),_c('br'),_c('p'),_vm._v("GP Photolab | Garland Pheasant\n鏡頭就像是信物花環，透過它看到的世界，充滿歡笑淚水幸福的色彩，任何的美好畫面都將被這以『愛』為基礎的圓所裱框。\n\n【Garland】\n花環在GP Photolab 的設定中，是情感的最高象徵。由各種素材環繞一圈都可稱之，因此它也是熱鬧、豐富、溫馨的象徵。\n花環，總是掛在每個家中的牆上，代表團圓、圓滿，我們服務無數家庭，在我心中卻都團聚成一個大家庭。\n\n【Pheasant】\n野雞，又稱雉雞，羽毛色澤極為漂亮，是鳳凰的原型。野雞是野生禽鳥，生命力及適應力強，對環境的要求不高，就像是我的心路歷程。\n對於攝影，一直都是用一股生命力與熱情去執行，並不愛專研器材跟動腦筋，無論怎樣的環境我都能繼續開心地投入拍照。\n雖然我很愛身著亮色感染周遭，那就像是野雞身上的羽毛，亮麗並不代表高貴，她依然嚮往最自然野生的生活，樸實的人們，南國的家。\n\n【Photolab】\n是底片時代具有沖洗技術的照相館，在數位的現在也許是個很復古的說法，但我們是一群熱愛最原始的底片膠卷的人，\n工作室每個攝影師都會延續這樣的膠卷精神。\n\n歡迎走進GP的照相館，讓我們為您拍張照吧！")])])],1),_c('vue-aos',{attrs:{"animation-class":"fadeIn animated"}},[_c('v-sheet',{staticClass:"mt-15 mb-15",attrs:{"color":"rgb(231 219 202)"}},[_c('v-slide-group',{staticClass:"pa-4",attrs:{"prev-icon":"mdi-minus","next-icon":"mdi-plus","show-arrows":"always"}},[_c('v-slide-item',{staticClass:"photography"},[_c('v-card',{staticClass:"p2 ma-4",at